.services-master {
  position: relative;
  background: #020617;
  padding: 120px 5%;
  min-height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* 3D Cyber Grid Background */
.cyber-grid-bg {
  position: absolute;
  inset: 0;
  background-image: 
    linear-gradient(to right, rgba(56, 189, 248, 0.05) 1px, transparent 1px),
    linear-gradient(to bottom, rgba(56, 189, 248, 0.05) 1px, transparent 1px);
  background-size: 50px 50px;
  mask-image: radial-gradient(circle at center, black, transparent 80%);
  transform: perspective(1000px) rotateX(60deg) translateY(-100px);
  z-index: 0;
}

.services-header {
  position: relative;
  z-index: 2;
  text-align: center;
  margin-bottom: 80px;
}

.pre-title {
  color: #38bdf8;
  text-transform: uppercase;
  letter-spacing: 5px;
  font-size: 14px;
  font-weight: 800;
}

.services-main-title {
  font-size: clamp(2.5rem, 6vw, 4rem);
  color: #fff;
  margin-top: 10px;
}

.text-highlight {
  color: #38bdf8;
  position: relative;
}

/* HYPER GRID */
.hyper-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 24px;
  max-width: 1300px;
  width: 100%;
  position: relative;
  z-index: 2;
}

/* HYPER CARD - The Apple/Vercel Style */
.hyper-card {
  position: relative;
  background: rgba(15, 23, 42, 0.5);
  border-radius: 24px;
  padding: 1px; /* Gap for border glow */
  overflow: hidden;
  transition: transform 0.3s ease;
  backdrop-filter: blur(12px);
}

.hyper-card:hover {
  transform: translateY(-10px);
}

.card-content {
  background: #0f172a;
  padding: 40px;
  border-radius: 23px;
  height: 100%;
  position: relative;
  z-index: 2;
}

.icon-box {
  width: 60px;
  height: 60px;
  background: rgba(56, 189, 248, 0.1);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 28px;
  color: #38bdf8;
  margin-bottom: 25px;
  transition: 0.5s;
}

.hyper-card:hover .icon-box {
  background: #38bdf8;
  color: #fff;
  box-shadow: 0 0 20px rgba(56, 189, 248, 0.5);
}

/* THE MOUSE TRACKING GLOW */
.hyper-card::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background: radial-gradient(
    600px circle at var(--mouse-x) var(--mouse-y),
    rgba(56, 189, 248, 0.3),
    transparent 40%
  );
  z-index: 1;
  opacity: 0;
  transition: opacity 0.5s;
}

.hyper-card:hover::before {
  opacity: 1;
}

.card-content h3 {
  color: white;
  font-size: 24px;
  margin-bottom: 15px;
}

.card-content p {
  color: #94a3b8;
  line-height: 1.6;
}

/* MOBILE ADAPTATION */
@media (max-width: 768px) {
  .services-master {
    padding: 80px 20px;
  }
  
  .hyper-card {
    background: #1e293b; /* Solidify for better mobile performance */
  }
  
  .cyber-grid-bg {
    display: none; /* Save GPU on mobile */
  }
}
/* MAGNETIC BUTTON STYLES */
.services-footer {
  margin-top: 100px;
  text-align: center;
  z-index: 10;
}

.services-footer p {
  color: #94a3b8;
  margin-bottom: 30px;
  font-size: 1.1rem;
  letter-spacing: 1px;
}

.magnetic-btn {
  position: relative;
  padding: 25px 60px;
  background: #38bdf8;
  border: none;
  border-radius: 100px;
  color: #020617;
  font-size: 18px;
  font-weight: 900;
  text-transform: uppercase;
  cursor: pointer;
  transition: transform 0.2s cubic-bezier(0.23, 1, 0.32, 1);
  display: inline-flex;
  align-items: center;
  gap: 15px;
  overflow: hidden;
}

.btn-text-inner {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
  gap: 10px;
}

.arrow {
  transition: transform 0.3s ease;
}

.magnetic-btn:hover .arrow {
  transform: translateX(8px);
}

/* The Animated Fluid Background inside the button */
.btn-blob {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 150%;
  height: 300%;
  background: #fff;
  border-radius: 40%;
  transform: translate(-50%, -50%) rotate(0deg);
  transition: transform 0.8s ease, top 0.5s ease;
  z-index: 1;
  opacity: 0;
}

.magnetic-btn:hover .btn-blob {
  opacity: 0.2;
  top: -50%;
  transform: translate(-50%, -50%) rotate(180deg);
}

/* PC vs Mobile performance */
@media (max-width: 768px) {
  .magnetic-btn {
    transform: none !important; /* Disable magnetic pull on touch screens */
    padding: 20px 40px;
    font-size: 16px;
  }
}
